.about {
  background-color: $color-default-background;
}

.about__container {
  max-width: 1260px;
  padding: 0 20px 117px 270px;
  margin: 0 auto;
  background-image: url("../img/background-2022.png");
  background-repeat: no-repeat;
  background-position: 20px top;

  @include vp-1023 {
    padding: 0 50px 78px 225px;
    background-position: left 45px top;
  }

  @include vp-767 {
    background: none;
    padding: 0 25px 80px;
  }
}

.about h2 {
  font-family: $ff-main;
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 12px;

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about p {
  font-family: $ff-secondary;
  font-size: 16px;
  line-height: 21px;
  margin: 0;

  &:not(:nth-of-type(2)) {
    margin-bottom: 22px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 18px;
  }
}

.about__text-mobile {
  display: none;
}

.about__text-tablet {
  @include vp-767 {
    display: none;
  }
}
